<!-- pages/checkout.vue -->
<template>
  <div class="mx-auto max-w-6xl px-4 py-6 lg:py-10">
    <h1 class="mb-6 text-2xl sm:text-3xl font-bold tracking-tight">
      {{ t('checkout.title') || 'Checkout' }}
    </h1>

    <!-- Empty cart -->
    <div
      v-if="itemsLen === 0"
      class="rounded-2xl border border-dashed bg-white p-10 text-center text-gray-600"
    >
      <p class="mb-3 text-base">
        {{ t('checkout.empty') || 'Your cart is empty. Add products before checking out.' }}
      </p>
      <NuxtLink
        to="/"
        class="inline-flex items-center gap-2 rounded-xl border px-4 py-2 text-sm font-medium hover:bg-gray-50"
      >
        ‚Üê {{ t('common.continueShopping') || 'Continue shopping' }}
      </NuxtLink>
    </div>

    <!-- Checkout content -->
    <div v-else class="grid gap-6 lg:grid-cols-12">
      <!-- Billing / details form -->
      <section class="lg:col-span-8 space-y-4">
        <div class="rounded-2xl border bg-white p-5 shadow-sm">
          <h2 class="text-base font-semibold mb-4">
            {{ t('checkout.details') || 'Billing details' }}
          </h2>

          <!-- we still keep a form for Enter key etc, but no submit button inside -->
          <form class="space-y-4" @submit.prevent="placeOrder">
            <div class="grid gap-4 sm:grid-cols-2">
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">
                  {{ t('checkout.fullName') || 'Full name' }}
                </label>
                <input
                  v-model="form.full_name"
                  type="text"
                  class="w-full rounded-xl border px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-orange-500"
                  required
                />
              </div>

            </div>

            <div class="grid gap-4 sm:grid-cols-2">
    
              <!-- Email (from auth, read-only) -->
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">
                  {{ t('checkout.email') || 'Email' }}
                </label>
                <input
                  :value="userEmail"
                  type="email"
                  disabled
                  class="w-full rounded-xl border px-3 py-2 text-sm bg-gray-50 text-gray-500 cursor-not-allowed"
                />
                <p class="mt-1 text-xs text-gray-500">
                  {{
                    t('checkout.emailFromAccount') ||
                    'Your email comes from your account and cannot be changed here.'
                  }}
                </p>
              </div>
            </div>

            <div>
              <label class="block text-sm font-medium text-gray-700 mb-1">
                {{ t('checkout.notes') || 'Order notes (optional)' }}
              </label>
              <textarea
                v-model="form.notes"
                rows="3"
                class="w-full rounded-xl border px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-orange-500"
                :placeholder="t('checkout.notesPlaceholder') || 'Any special instructions‚Ä¶'"
              />
            </div>

            <!-- Payment methods -->
            <div class="pt-4 border-t">
              <h3 class="text-sm font-semibold mb-2">
                {{ t('checkout.paymentMethod') || 'Payment method' }}
              </h3>

              <div class="space-y-2">
                <!-- PayPal -->
                <label
                  class="flex items-start gap-3 p-3 rounded-xl border cursor-pointer hover:border-orange-400 transition"
                  :class="{ 'border-orange-500 bg-orange-50/40': form.payment_method === 'paypal' }"
                >
                  <input
                    type="radio"
                    value="paypal"
                    v-model="form.payment_method"
                    class="mt-1"
                  />
                  <div class="flex items-start gap-3">
                    <!-- Icon -->
                    <div
                      class="mt-0.5 h-8 w-8 rounded-full bg-[#00457C] text-white flex items-center justify-center text-xs font-semibold"
                    >
                      P
                    </div>
                    <!-- Text -->
                    <div class="text-sm">
                      <div class="font-medium">
                        {{ t('checkout.methods.paypal.title') || 'PayPal' }}
                      </div>
                      <div class="text-xs text-gray-600">
                        {{
                          t('checkout.methods.paypal.desc') ||
                          'An invoice will be created in PayPal and sent to your email.'
                        }}
                      </div>
                    </div>
                  </div>
                </label>

                <!-- USDT transfer -->
                <label
                  class="flex items-start gap-3 p-3 rounded-xl border cursor-pointer hover:border-orange-400 transition"
                  :class="{ 'border-orange-500 bg-orange-50/40': form.payment_method === 'usdt' }"
                >
                  <input
                    type="radio"
                    value="usdt"
                    v-model="form.payment_method"
                    class="mt-1"
                  />
                  <div class="flex-1 flex items-start gap-3">
                    <!-- Icon -->
                    <div
                      class="mt-0.5 h-8 w-8 rounded-full bg-emerald-600 text-white flex items-center justify-center text-xs font-semibold"
                    >
                      ‚ÇÆ
                    </div>

                    <!-- Text -->
                    <div class="flex-1 text-sm">
                      <div class="font-medium">
                        {{ t('checkout.methods.usdt.title') || 'USDT transfer' }}
                      </div>
                      <div class="text-xs text-gray-600">
                        {{
                          t('checkout.methods.usdt.desc') ||
                          'Send USDT to our wallet and WhatsApp us the proof of payment.'
                        }}
                      </div>

                      <div
                        v-if="form.payment_method === 'usdt'"
                        class="mt-2 text-xs text-gray-700 space-y-1"
                      >
                        <div class="font-semibold">
                          {{
                            t('checkout.methods.usdt.walletLabel') ||
                            'USDT wallet address'
                          }}
                        </div>

                        <!-- Wallet + copy -->
                        <div
                          class="rounded-lg bg-gray-100 px-3 py-2 break-all text-[11px] flex items-center justify-between gap-2"
                        >
                          <span class="truncate">
                            {{ usdtWallet || 'USDT WALLET !' }}
                          </span>

                          <button
                            type="button"
                            class="inline-flex items-center rounded-lg border border-gray-300 bg-white px-2 py-1 text-[11px] font-medium hover:bg-gray-50"
                            @click.stop="copyWallet"
                          >
                            <!-- clipboard icon -->
                            <svg
                              class="h-3 w-3"
                              viewBox="0 0 24 24"
                              fill="none"
                              stroke="currentColor"
                              stroke-width="2"
                              stroke-linecap="round"
                              stroke-linejoin="round"
                            >
                              <rect
                                x="9"
                                y="9"
                                width="13"
                                height="13"
                                rx="2"
                                ry="2"
                              ></rect>
                              <path
                                d="M5 15H4a2 2 0 0 1-2-2V4a2 2 0 0 1 2-2h9a2 2 0 0 1 2 2v1"
                              ></path>
                            </svg>
                            <span class="ml-1">
                              {{
                                copiedWallet
                                  ? t('checkout.copied') || 'Copied'
                                  : t('checkout.copy') || 'Copy'
                              }}
                            </span>
                          </button>
                        </div>

                        <div v-if="copiedWallet" class="text-[11px] text-emerald-600">
                          {{
                            t('checkout.walletCopied') ||
                            'Wallet address copied to clipboard.'
                          }}
                        </div>

                        <div class="text-[11px] text-gray-600">
                          {{
                            t('checkout.methods.usdt.afterPayment', {
                              phone: whatsappNumber || 'WhatsApp',
                            }) ||
                            'After sending USDT, please send the payment proof to us on WhatsApp.'
                          }}
                        </div>
                      </div>
                    </div>
                  </div>
                </label>

                <!-- International transfer -->
                <label
                  class="flex items-start gap-3 p-3 rounded-xl border cursor-pointer hover:border-orange-400 transition"
                  :class="{ 'border-orange-500 bg-orange-50/40': form.payment_method === 'transfer' }"
                >
                  <input
                    type="radio"
                    value="transfer"
                    v-model="form.payment_method"
                    class="mt-1"
                  />
                  <div class="flex items-start gap-3">
                    <!-- Icon -->
                    <div
                      class="mt-0.5 h-8 w-8 rounded-full bg-slate-700 text-white flex items-center justify-center text-xs font-semibold"
                    >
                      $
                    </div>
                    <!-- Text -->
                    <div class="text-sm">
                      <div class="font-medium">
                        {{
                          t('checkout.methods.transfer.title') ||
                          'International transfer'
                        }}
                      </div>
                      <div class="text-xs text-gray-600">
                        {{
                          t('checkout.methods.transfer.desc') ||
                          'You can pay via Western Union or international bank transfer. We will send you the details after you place the order.'
                        }}
                      </div>
                    </div>
                  </div>
                </label>
              </div>
            </div>
          </form>
        </div>
      </section>

      <!-- Summary -->
      <aside class="lg:col-span-4">
        <div class="lg:sticky lg:top-24 overflow-hidden rounded-2xl border bg-white shadow-sm">
          <div class="border-b bg-gradient-to-r from-orange-100 to-amber-50 px-5 py-3">
            <h2 class="text-base font-semibold">
              {{ t('checkout.summary') || 'Order summary' }}
            </h2>
          </div>

          <div class="p-5 space-y-3">
            <ul class="space-y-2 max-h-56 overflow-y-auto pr-1 text-sm">
              <li
                v-for="it in itemsRef"
                :key="rowKey(it)"
                class="flex items-start justify-between gap-3"
              >
                <!-- Image -->
                <div class="shrink-0">
                  <NuxtImg
                    v-if="it?.image"
                    :src="it.image"
                    width="48"
                    height="48"
                    :modifiers="{ fit: 'cover' }"
                    class="h-12 w-12 rounded-lg border bg-gray-50 object-cover"
                    alt=""
                  />
                  <div
                    v-else
                    class="h-12 w-12 rounded-lg border bg-gray-50 grid place-items-center text-gray-400 text-xs"
                  >
                    ‚Äî
                  </div>
                </div>

                <!-- Text -->
                <div class="flex-1 min-w-0">
                  <div class="line-clamp-2 font-medium text-gray-900">
                    {{ it?.name }}
                  </div>
                  <div class="text-xs text-gray-500">
                    x{{ it?.qty }} ¬∑ ${{ price(it?.price) }}
                  </div>
                </div>

                <!-- Line total -->
                <div class="text-right tabular-nums font-semibold">
                  ${{ price(lineTotal(it)) }}
                </div>
              </li>
            </ul>

            <div class="flex justify-between text-sm pt-2 border-t">
              <span class="text-gray-600">
                {{ t('cart.subtotal') || 'Subtotal' }}
              </span>
              <span class="font-medium tabular-nums">
                ${{ price(subtotal) }}
              </span>
            </div>

            <div class="border-t pt-3 flex justify-between items-center">
              <span class="text-base font-semibold">
                {{ t('cart.total') || 'Total' }}
              </span>
              <span class="text-2xl font-bold tabular-nums">
                ${{ price(subtotal) }}
              </span>
            </div>

            <p class="text-[11px] text-gray-500 pt-1">
              {{
                t('checkout.info') ||
                'You will be contacted or redirected to complete the payment after placing your order.'
              }}
            </p>

            <!-- Terms + button under total -->
            <div class="mt-3 space-y-2 border-t pt-3">
              <label class="flex items-start gap-2 text-xs text-gray-700">
                <input
                  v-model="form.accept_terms"
                  type="checkbox"
                  class="mt-0.5"
                />
                <span v-html="acceptTermsHtml"></span>
              </label>

              <div v-if="errorMsg" class="rounded-xl bg-red-50 px-3 py-2 text-xs text-red-700">
                {{ errorMsg }}
              </div>
              <div
                v-if="successMsg"
                class="rounded-xl bg-emerald-50 px-3 py-2 text-xs text-emerald-700"
              >
                {{ successMsg }}
              </div>

              <button
                type="button"
                class="btn-primary w-full mt-1"
                :disabled="placing || !canSubmit"
                @click="placeOrder"
              >
                <span v-if="placing">
                  {{ t('checkout.placing') || 'Placing order‚Ä¶' }}
                </span>
                <span v-else>
                  {{ t('checkout.placeOrder') || 'Place order' }}
                </span>
              </button>
            </div>
          </div>
        </div>
      </aside>
    </div>
  </div>
</template>

<script setup lang="ts">
const { t } = useI18n()
const cart = useCart()
const router = useRouter()
const { $api } = useNuxtApp()
const auth = (typeof useAuth === 'function' ? useAuth() : null) as any

const user = computed(() => auth?.user?.value)
const userEmail = computed(() => (user.value as any)?.email || '')

const runtimeConfig = useRuntimeConfig()
const usdtWallet = computed(() => runtimeConfig.public?.usdtWallet || '')
const whatsappNumber = computed(() => runtimeConfig.public?.whatsappNumber || '')

/** Normalize cart items */
const itemsRef = computed<any[]>(() => {
  const raw = (cart as any)?.items
  if (Array.isArray(raw?.value)) return raw.value as any[]
  if (Array.isArray(raw)) return raw as any[]
  return [] as any[]
})
const itemsLen = computed(() => itemsRef.value.length)

/** Summary */
const subtotal = computed(() =>
  itemsRef.value.reduce(
    (sum, i: any) => sum + Number(i?.price || 0) * Number(i?.qty || 0),
    0
  )
)

const price = (n: number | string) => Number(n || 0).toFixed(2)
const lineTotal = (it: any) => Number(it?.price || 0) * Number(it?.qty || 0)

/** cart row key */
const cartId = (it: any) =>
  it?.id ?? `${it?.product_type}:${it?.product_id}` // <-- fixed template literal
const rowKey = (it: any) => String(cartId(it))

/** Form + submit */
const form = reactive({
  full_name: '',
  company: '',
  vat_number: '',
  notes: '',
  payment_method: '' as '' | 'paypal' | 'usdt' | 'transfer',
  accept_terms: false,
})

onMounted(() => {
  if (user.value) {
    const u: any = user.value
    form.full_name =
      u.name || [u.first_name, u.last_name].filter(Boolean).join(' ') || ''
  }
})

const placing = ref(false)
const errorMsg = ref('')
const successMsg = ref('')

const acceptTermsHtml = computed(
  () =>
    t('checkout.acceptTermsHtml') ||
    'I accept the <a href="/terms" class="text-orange-600 underline" target="_blank">terms &amp; conditions</a>.'
)

/** Copy wallet */
const copiedWallet = ref(false)
async function copyWallet() {
  if (!usdtWallet.value) return
  try {
    await navigator.clipboard.writeText(usdtWallet.value)
    copiedWallet.value = true
    setTimeout(() => {
      copiedWallet.value = false
    }, 2000)
  } catch (e) {
    console.error('Failed to copy wallet address', e)
  }
}

/** canSubmit: controls disabled state of button */
const canSubmit = computed(() => {
  return (
    !!form.full_name &&
    !!userEmail.value &&
    !!form.payment_method &&
    form.accept_terms &&
    itemsLen.value > 0 &&
    !placing.value
  )
})

async function placeOrder() {
  if (!canSubmit.value) return

  placing.value = true
  errorMsg.value = ''
  successMsg.value = ''

  try {
    const payload = {
      full_name: form.full_name,
      company: form.company,
      vat_number: form.vat_number,
      notes: form.notes,
      payment_method: form.payment_method,
      accept_terms: form.accept_terms,
      email: userEmail.value,
      items: itemsRef.value.map((it) => ({
        id: it.id,
        product_id: it.product_id,
        qty: it.qty,
        unit_price: it.price, // ‚úÖ send discounted price
      })),

    }

    // üîÅ use $api the SAME WAY as in login.vue
    const res: any = await $api('/checkout', {
      method: 'POST',
      body: payload,
    })

    if (form.payment_method === 'paypal') {
      successMsg.value =
        t('checkout.successPaypal') ||
        'Your PayPal invoice has been created. Redirecting you to PayPal...'

      if (res?.paypal_invoice_url) {
        // redirect same tab (best UX)
        window.location.href = res.paypal_invoice_url
      } else {
        successMsg.value =
          t('checkout.successPaypalNoUrl') ||
          'Your PayPal invoice was created and sent to your email. Please check your inbox to pay.'
      }
    }
    else if (form.payment_method === 'usdt') {
      successMsg.value =
        t('checkout.successUsdt') ||
        'Your order has been created. Please send the USDT transfer and WhatsApp us the proof of payment.'
    } else {
      successMsg.value =
        t('checkout.successTransfer') ||
        'Your order has been created. We will contact you with international transfer details.'
    }

    // ;(cart as any).clear?.()
    // ;(cart as any).reload?.()
    // optionally:
    // await router.push('/thank-you')
  } catch (e: any) {
    console.error(e)
    // $api errors look like the ones you handled on login page
    errorMsg.value =
      e?.data?.message ||
      e?.message ||
      (t('checkout.error') as string) ||
      'Something went wrong while placing your order.'
  } finally {
    placing.value = false
  }
}
</script>

<style scoped>
.btn-primary {
  @apply inline-flex items-center justify-center rounded-xl bg-orange-500 text-white px-4 py-2 font-medium hover:bg-orange-600 transition disabled:opacity-60 disabled:cursor-not-allowed;
}
.tabular-nums {
  font-variant-numeric: tabular-nums;
}
</style>
